@import url(https://fonts.googleapis.com/css?family=PT+Sans);
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
  margin: 0;
  padding: 0;


  height: 100%;
  
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Main {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;

  font-family: 'PT Sans';

  display: flex;
  flex-direction: column;
  align-items: stretch;
  
  background-color: #202020;

}

.Header {
  height: 10vh;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  position: relative;

  color: white;

}
.time-style {
  font-size: 3vh;
  font-weight: bold;
  
}
.link-container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;  
}

.link-style-inactive {
  cursor: pointer;
  font-weight: bold;;
  font-size: 3vh;
  margin: auto 5%;
  color: white;
  opacity: 0.6;
  text-decoration: none;
}

.link-style {
  cursor: pointer;
  font-weight: bold;;
  font-size: 3vh;
  margin: auto 5%;
  color: white;

  -webkit-text-decoration-color: lightskyblue;

          text-decoration-color: lightskyblue;
}

.social-style {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: auto 0;
  display: flex;
}
.social-style img{
  width: 5vh;
  height: 5vh;
  padding: 0 20%;
}

.Body {
  width: 100%;
  height: 90%;

  padding: 0;
  margin: 0;

  background-color: #202020;
}


.Footer {
  width: 100%;
  height: 5%;

  position: fixed;
  bottom: 0;

  display: flex;
  justify-content: space-evenly;

  background-color: grey;

}







.main {
    display: flex;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 80%;
    margin: 1% auto 5% auto;
    
}

.left {
    width: 30%;
    height: auto;
    display: flex;
    flex-direction: column;


}
.left p {
    color: white;
    font-size: 1.8em;
    font-weight: bold;
    margin: 10% auto;
}
.left img {
    height: 65%;
    width: 75%;
    margin: 0 auto;
    border-radius: 20px;
    border-style: solid;
    border-color: black;

}

.right {
    height: auto;
    width: 70%;    
    display: flex;
    flex-direction: column;
    margin-top: 3%;
    
}

.right-header-component {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    font-size: 4vh;
    margin: auto;
    
    cursor: pointer;

}

.selected {
    text-align: center;
    width: 100%;
    color: white;
    text-decoration: underline;
    -webkit-text-decoration-style: solid;
            text-decoration-style: solid;
    -webkit-text-decoration-color: lightskyblue;
            text-decoration-color: lightskyblue;
    margin: 0;
}
.notSelected {
    text-align: center;
    width: 100%;
    color: white;
    margin: 0;

    text-decoration: none;
    opacity: 0.4;
}

.right-body-component {
    width: 100%;
    height: 100%;
    color: white;
    
}

.right-intro-p {
    font-size: 1.2em;
    margin: 0 0 3% 5%;
}

.right-skills {
    color:white;
    margin: 5% 0;

}

.right-skills li {
    margin: 0 0 2% 7%;
    float: left;
    list-style-type: circle;
}

.right-interests {
    text-align: center; 
    height: auto;
    padding: 3% 0 0 10%;
    margin: auto;
    text-align: justify;
}

.right-interests p {
    font-size: 1.2em;
    
}

.main-experience {
    color: white;
    width: 100%;
    
    display: flex;
    flex-direction: row;

    margin-bottom: 3%;
}

.jobicon {
    width: 50px;
    height: 50px;
    padding-top: 30%;
}

.experience-left {
    display: flex;
    flex-direction: column;
}

.experience-left-bottom {
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    background: -webkit-gradient(linear, left top, left bottom, from(white), to(white)) no-repeat center/2px 100%;
    background: linear-gradient(white, white) no-repeat center/2px 100%;
    background-color: black;
}

.test {
    width: 100%;
    margin: 0 0 0 50%;
    padding: 0;
}

.experience-right {
    padding: 1% 2%;

 
    border-bottom: 2px solid white;

}

.experience-right-bottom {
    font-size: 1.1em;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;

    padding: 0 2vh;
    margin: 0 1vh 0 0;

    border-radius: 15px;
    background-color: darkslategrey;
}
.projects-container {    
    height: -webkit-fit-content;    
    height: -moz-fit-content;    
    height: fit-content;
    width: 80%;
    
    margin: 5% auto;


    display: grid;
    grid-template-columns: 40% 40%;

    grid-gap: 15% 5%;

    justify-content: center;


}

.projects-element {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 100%;
    
    text-align: center;
    font-size: 1.5em;    
    color: black;
    background-color: lightskyblue;
    border-style: solid;
    border-width: 5px;
    cursor: pointer;
}

.project-popup {
    position: absolute;
    
    margin: 1% 0 1% 0;

    width: 40%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    border-style: solid;

    color: white;
    background-color: darkslategrey;
    border-radius: 20px;
}

.diplay-none {
    display: none;
}

.project-header {
    font-size: 1.5em;    
    margin: 0 0 1vh 0;
    font-weight: bold;
}

.test {

    text-align: left;
    width: 100%;
    margin: 0 0 1.5vh 0;
    padding: 0;
}

.testButton{
    box-shadow:inset 0px 1px 3px 0px #91b8b3;
	background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #768d87), to(#6c7c7c));
	background:linear-gradient(to bottom, #768d87 5%, #6c7c7c 100%);
	background-color:#768d87;
	border-radius:5px;
	border:1px solid #566963;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:15px;
	font-weight:bold;
	padding: 7px 15px;
	text-decoration:none;
    text-shadow:0px -1px 0px #2b665e;
    
}

.testButton2{
    font-weight: bold;
    font-size: 1.2em;
	background-color:white;
	border-radius:5px;
	border:1px solid #566963;
}
.main-contact {
    width: 70%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: 5% auto;

    
    display: flex;
    flex-direction: row;

    
}

.contact-left {
    width: 40%;

    font-size: 3em;
    color: white;
    text-align: center;
    margin: auto;
}

.contact-right {
    width: 60%;
    height: 100%;
    display: flex;
    flex-direction: column;
    color: white;
    
}

.contact-right form{
    
    margin: auto;
    width: 90%;

}


.contact-right p{
    font-size: 2em;
    padding: 0;
    margin: auto;

}

.contact-right textarea{
    font-size: 1.5em;
    text-align: left;
    margin-bottom: 5%;
    width: 80%;
    height: 1.5em;
}
